@import "../../scss/var.scss";

.wrapper {
  width: 100%;
  .grid {
    width: 100%;
    display: grid;
    gap: 15px;

    .item {
      background-color: $primary;
      position: relative;
      min-height: 200px;
      .header {
        position: absolute;
        top: 15px;
        left: 15px;
        max-width: calc(50% - 30px);
        .title {
          font-weight: 600;
        }
      }
      .img {
        position: absolute;
        bottom: 0px;
        right: 15px;
        display: block;
        max-width: 50%;
        max-height: 70%;
      }
      &.sizeRow {
        grid-row: span 2;
        .img {
          width: calc(100% - 30px);
          max-width: calc(100% - 30px);
        }
        .header {
          max-width: calc(100% - 30px);
          width: calc(100% - 30px);
        }
      }
    }
  }
}
